@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
  /* Modern Browsers */
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensans.woff") format("woff"), url("../fonts/opensans.woff2") format("woff2");
  /* Modern Browsers */
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/opensanslight.woff") format("woff"), url("../fonts/opensanslight.woff2") format("woff2");
  /* Modern Browsers */
  font-weight: 300;
  font-style: normal; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 30px;
  color: #284462;
  background-color: #f2f2f2;
  position: relative; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  background: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 0%;
  display: flex;
  flex-direction: column;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #ffffff;
  position: relative; }

.header__nav {
  position: relative;
  background-color: #284462;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 100%; }

.header__logo {
  min-height: 69px;
  border-top: 1px solid rgba(255, 255, 255, 0.15); }

.header__img {
  margin: 20px 0 0 20px; }
  .header__img:active {
    opacity: 0.3; }

@media (min-width: 768px) and (max-width: 1199px) {
  .header {
    background: url("../img/bg-back-tablet@1x.jpg");
    background-repeat: no-repeat; }
  .header__logo {
    min-height: 109px; }
  .header__img {
    margin: 27px 0 0 20px;
    width: 147px;
    height: 40px; } }

@media (min-width: 1200px) {
  .header {
    padding: 0 130px 0 130px;
    background: url("../img/bg-back-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover; }
  .header__nav {
    flex-direction: row;
    justify-content: space-between;
    background-color: transparent; }
  .header__logo {
    display: flex;
    align-items: center;
    border-top: none; }
  .header__img {
    margin: 0;
    width: 147px;
    height: 40px; } }

.nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 80px;
  height: 66px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  padding: 0;
  margin: 0; }
  .nav__toggle:focus {
    outline: none; }

.nav__toggle--closed::before {
  content: "";
  position: absolute;
  top: 21px;
  right: 20px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff; }

.nav__toggle--opened::before,
.nav__toggle--opened::after {
  content: "";
  position: absolute;
  top: 20px;
  right: 30px;
  width: 5px;
  height: 30px;
  background-color: #ffffff; }

.nav__toggle--opened::before {
  transform: rotate(45deg); }

.nav__toggle--opened::after {
  transform: rotate(-45deg); }

.nav__list {
  position: absolute;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #284462; }
  .nav__list:last-child {
    border-bottom: 1px solid black; }

.nav__link {
  display: inline-block;
  width: 100vw;
  text-align: center;
  font-size: 18px;
  line-height: 62px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  color: white;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 -2px rgba(0, 0, 0, 0.4); }
  .nav__link:visited {
    color: white; }
  .nav__link:focus {
    outline: none; }
  .nav__link:active {
    color: rgba(210, 40, 85, 0.3); }

/*
@media (min-width: $tablet-width) {

}*/
@media (min-width: 768px) and (max-width: 1199px) {
  .nav__toggle {
    width: 100px;
    height: 100px; }
  .nav__toggle--closed::before {
    top: 45px;
    right: 20px; }
  .nav__toggle--opened::before,
  .nav__toggle--opened::after {
    top: 44px;
    right: 30px; } }

@media (min-width: 1200px) {
  .nav__toggle {
    display: none; }
  .nav__list {
    display: flex;
    position: static;
    flex-direction: row;
    justify-content: space-between;
    width: 50vw;
    background-color: transparent; }
    .nav__list:last-child {
      border-bottom: none; }
  .nav__link {
    width: auto;
    line-height: 109px;
    font-weight: 400;
    text-transform: none;
    border-top: none;
    box-shadow: none;
    padding: 0; }
    .nav__link:hover {
      cursor: pointer;
      color: #d22855; } }

.presentation {
  display: flex;
  flex-direction: column; }

.presentation__look {
  padding: 29px 20px 30px 20px;
  min-height: 395px;
  box-sizing: border-box;
  position: relative; }

.presentation__heading {
  color: white;
  font-family: "OpenSans", "Verdana", sans-serif;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
  margin: 0;
  padding: 0; }

.presentation__img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 257px;
  height: 286px; }

@media (min-width: 768px) {
  .presentation {
    justify-content: center; } }

@media (min-width: 768px) and (max-width: 1199px) {
  .presentation {
    max-width: 48vw;
    min-height: 620px; }
  .presentation__look {
    position: static;
    min-height: 0; }
  .presentation__heading {
    font-size: 40px;
    line-height: 45px;
    margin: 10px 60px 10px 60px; }
  .presentation__img {
    height: 545px;
    width: auto;
    bottom: 1px;
    right: 0;
    left: auto; } }

@media (min-width: 1200px) {
  .presentation {
    max-width: 25vw;
    min-height: 742px; }
  .presentation__look {
    position: static;
    padding: 45px 0 45px 0;
    line-height: 60px;
    min-height: 0; }
  .presentation__heading {
    font-size: 50px;
    max-width: 80%;
    margin: 0 auto; }
  .presentation__img {
    height: 645px;
    width: auto;
    bottom: 0;
    right: 0;
    left: auto; } }

.application {
  position: relative;
  padding: 29px 20px 35px 20px;
  background-color: #1d2631; }

.application__link {
  display: block;
  padding: 0;
  margin: 0 auto;
  color: white;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 48px;
  text-decoration: none;
  text-align: center;
  border: 4px solid #ffffff;
  border-radius: 27px;
  max-width: 300px; }

.application__system {
  position: relative;
  margin: 0 auto;
  padding: 30px;
  box-sizing: border-box;
  min-height: 140px;
  list-style: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 300px; }

.application__system-link {
  display: block;
  color: transparent;
  font-size: 0; }

.application__system-link--mac {
  background: url("../img/logo-apple.svg") no-repeat;
  width: 47px;
  height: 56px; }

.application__system-link--android {
  background: url("../img/logo-android.svg") no-repeat;
  width: 43px;
  height: 47px; }

.application__system-link--win {
  background: url("../img/logo-microsoft.svg") no-repeat;
  width: 44px;
  height: 41px; }

.application__text {
  margin: 0;
  padding: 0;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  text-align: center; }

@media (min-width: 768px) {
  .application {
    padding: 0;
    background-color: transparent; }
  .application__text {
    font-size: 18px; } }

@media (min-width: 768px) and (max-width: 1199px) {
  .application {
    box-sizing: border-box;
    padding: 20px; }
  .application__system {
    min-height: 110px; } }

@media (min-width: 1200px) {
  .application__link {
    margin: 0;
    max-width: 100%; }
  .application__system {
    min-height: 180px; } }
